<template>
  <div>
    <div class="sidenav">
      <a style="direction:rtl;text-align:right;font-family: 'Segoe UI', Tahoma;font-size: 17px" @click="show_Filters">مشاهده
        ی فیلتر ها</a>
      <a style="direction:rtl;text-align:right;font-family: 'Segoe UI', Tahoma;font-size: 17px" @click="show_Stocks">مشاهده
        سهم ها</a>
      <a style="direction:rtl;text-align:right;font-family: 'Segoe UI', Tahoma;font-size: 17px"
         @click="show_favorite_filters">فیلتر های منتخب</a>
      <a style="direction:rtl;text-align:right;font-family: 'Segoe UI', Tahoma;font-size: 17px"
         @click="show_favorite_stocks">سهم های منتخب</a>

      <div style="margin-top: 40px">
        <h1
          style="direction:rtl;text-align:right;font-size: 14px;color: #818181;margin-right: 6px;font-family: 'Segoe UI', Tahoma;font-size: 12px">
          عدم نمایش فیلتر های
          میانگین 12 ماهه
        </h1>
        <label class="switch" style="margin-left: 4px">
          <input type="checkbox" @click="mean12_Switch">
          <span class="slider round"></span>
        </label>

      </div>
      <div style="margin-top: 10px">
        <h1
          style="direction:rtl;text-align:right;font-size: 15px;color: #818181;margin-right: 6px;font-family: 'Segoe UI', Tahoma;font-size: 12px">
          عدم نمایش فیلتر های
          میانگین 3 ماهه
        </h1>
        <label class="switch" style="margin-left: 4px">
          <input type="checkbox" @click="mean3_Switch">
          <span class="slider round"></span>
        </label>
      </div>

      <p style="font-family: 'Segoe UI', Tahoma">از تاریخ</p>
      <div>
        <date-picker v-model="date_start" @change="date_start_changed"></date-picker>
      </div>
      <p style="font-family: 'Segoe UI', Tahoma"> تا تاریخ</p>
      <div>
        <date-picker v-model="date_end" @change="date_end_changed"></date-picker>
      </div>

      <button class="button button3" style="width: 90px;height: 30px;margin-top: 30px" @click="log_out">خروج</button>

    </div>


    <tr v-for="item in stocks" style="direction:rtl">
      <p @click="getStock(item)" style=" margin-left: 1080px;font-size: 17px;font-family: 'Segoe UI', Tahoma">{{ item
        }}</p>
      <button class="button button3" style="width: 220px;height: 30px;margin-right: 0px;background-color: forestgreen"
              @click="add_to_favorites_stocks(item)">به سهم های منتخب اضافه شود
      </button>
      <button class="button button3" style="width: 220px;height: 30px;margin-right: 0px"
              @click="remove_favorites_stocks(item)">از سهم های منتخب حذف شود
      </button>
    </tr>


    <tr v-for="item in filters" style="direction:rtl">
      <p @click="getFilter(item)" style="margin-left: 0px;font-size: 17px;font-family: 'Segoe UI', Tahoma">{{ item
        }}</p>
      <button class="button button3" style="width: 220px;height: 30px;margin-right: 900px;background-color: forestgreen"
              @click="add_to_favorites_filters(item)">به فیلتر های منتخب اضافه شود
      </button>
      <button class="button button3" style="width: 220px;height: 30px;margin-right: 900px"
              @click="remove_favorites_filters(item)">از فیلتر های منتخب حذف شود
      </button>

    </tr>


    <a style="color: #262626 ;font-size: 20px ; margin-left: 5px;font-family: 'Segoe UI', Tahoma;"> {{ page_info }}</a>


    <table>
      <tr style="background-color: #444444;direction:rtl;text-align:right;">
        <th v-for="item in column_names"
            style="direction:rtl;text-align:right;color: #111111;font-family: 'Segoe UI', Tahoma;"> {{ item }}
        </th>
      </tr>
      <tr v-for="item in search_result" style="text-align:right;font-family: 'Segoe UI', Tahoma;">
        <td v-for="items in item.data"
            v-if="(item.isMean3 == '0' || item.isMean3 == null) && (item.isMean12 == '0' || item.isMean12 == null)"
            style="text-align:right;">
          <a v-if="item.isNegative == '1' && items.isLink == '0'"
             style="color: darkred;font-size: 17px;">{{ items.data
            }}</a>
          <a v-if="item.isNegative == '0' && items.isLink == '1'"
             style="color: darkgreen;font-size: 17px">
            <p v-if="items.isFilter == '1'" @click="getStock(items.data)">{{ items.data }}</p>
            <p v-if="items.isStock == '1'" @click="getFilter(items.data)">{{ items.data }}</p>
          </a>
          <a v-if=" item.isNegative== '1' && items.isLink == '1'"
             style="color: darkred;font-size: 17px"><p
            v-if="items.isFilter == '1'" @click="getStock(items.data)">{{ items.data }}</p>
            <p v-if="items.isStock == '1'" @click="getFilter(items.data)">{{ items.data }}</p></a>
          <a v-if="item.isNegative == '0' && items.isLink == '0'"
             style="color: darkgreen;font-size: 17px">{{ items.data
            }}</a>
        </td>
      </tr>
    </table>


  </div>
</template>


<style>
  .button {
    background-color: darkred;
    border: none;
    color: white;
    font-family: 'Segoe UI', Tahoma;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
  }


  .button3 {
    border-radius: 8px;
  }


  body {
    font-family: "Lato", sans-serif;
    font-family: 'Segoe UI', Tahoma;

    font-size: 14px;
  }

  /* Fixed sidenav, full height */
  .sidenav {
    height: 100%;
    width: 200px;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 20px;
  }

  /* Style the sidenav links and the dropdown button */
  .sidenav a, .dropdown-btn {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 20px;
    color: #818181;
    display: block;
    border: none;
    background: none;
    width: 100%;
    text-align: left;
    margin-left: -30px;
    cursor: pointer;
    outline: none;

  }

  /* On mouse-over */
  .sidenav a:hover, .dropdown-btn:hover {
    color: #f1f1f1;
  }

  /* Main content */
  .main {
    text-align: right;
    margin-right: 200px; /* Same as the width of the sidenav */
    font-size: 20px; /* Increased text to enable scrolling */
    padding: 0px 10px;

  }

  /* Add an active class to the active dropdown button */
  .active {
    background-color: green;
    color: white;
  }

  /* Dropdown container (hidden by default). Optional: add a lighter background color and some left padding to change the design of the dropdown content */
  .dropdown-container {
    display: none;
    background-color: #262626;
    padding-left: 8px;
  }

  /* Optional: Style the caret down icon */
  .fa-caret-down {
    float: right;
    padding-right: 8px;
  }

  /* Some media queries for responsiveness */
  @media screen and (max-height: 450px) {
    .sidenav {
      padding-top: 15px;
    }

    .sidenav a {
      font-size: 18px;
    }
  }

  #app {

  }

  table {

    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    margin-left: -30px;
    border-right: 200px solid #ddd;
    border-top: 0px solid #ddd;
    background-color: lightslategrey;

  }

  th, td {
    text-align: left;
    padding: 6px;
  }

  tr:nth-child(even) {
    background-color: grey;

  }


  p {
    cursor: pointer;
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 24px;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
  }

  .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 2px;
    bottom: 2px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  }

  input:checked + .slider {
    background-color: #2196F3;
  }

  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(16px);
    -ms-transform: translateX(16px);
    transform: translateX(16px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

</style>


<script src="./Scripts/MdBootstrapPersianDateTimePicker/calendar.js" type="text/javascript"></script>
<script src="./Scripts/MdBootstrapPersianDateTimePicker/jquery.Bootstrap-PersianDateTimePicker.js"
        type="text/javascript"></script>
<script src="./Scripts/jquery-2.1.4.js" type="text/javascript"></script>
<script src="./Scripts/bootstrap.min.js" type="text/javascript"></script>

<script>
  import axios from "axios"
  import VuePersianDatetimePicker from 'vue-persian-datetime-picker'
  import router from "../router";

  export default {
    name: 'blog',

    data() {

      return {
        stocks: [],
        filters: [],
        search_result: [],
        column_names: [],
        page_info: '',
        mean3_Flag: 0,
        mean12_Flag: 0,
        date_start: '',
        date_end: '',
        stocks_buffer: [],
        filters_buffer: [],
        stocks_search_buffer: {},
        filters_search_buffer: {},
        column_names_buffer: [],
        page_ino_buffer: '',
        config: {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          }
        }
      }
    },
    components: {
      datePicker: VuePersianDatetimePicker
    },
    methods: {

      show_Stocks() {

        this.stocks = []
        this.filters = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''

        if (localStorage.getItem('stocks') == [] || localStorage.getItem('stocks') === null) {
          axios.get("http://127.0.0.1:8000/stocks/", this.config).then((response) => {
            if (response.data == "Token expired") {
              alert('لطفا دوباره وارد شوید')
              router.push('/')
            }

            var temp = []
            for (var i = 0; i < response.data.stocks.length; i++) {
              temp.push(response.data.stocks[i])
            }
            localStorage.setItem('stocks', JSON.stringify(temp))
            var stored = JSON.parse(localStorage.getItem("stocks"))
            this.stocks = stored
          })
        } else {
          var stored = JSON.parse(localStorage.getItem("stocks"))
          this.stocks = stored
        }

      },
      show_Filters() {

        this.filters = []
        this.stocks = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''

        if (localStorage.getItem('filters') == [] || localStorage.getItem('filters') === null) {
          var temp = []
          axios.get("http://127.0.0.1:8000/filters/", this.config).then((response) => {
            if (response.data == "Token expired") {
              alert('لطفا دوباره وارد شوید')
              router.push('/')
            }
            for (var i = 0; i < response.data.filters.length; i++) {
              temp.push(response.data.filters[i])
            }
            localStorage.setItem('filters', JSON.stringify(temp))
            var stored = JSON.parse(localStorage.getItem("filters"))
            this.filters = stored
          })

        } else {
          var stored = JSON.parse(localStorage.getItem("filters"))
          this.filters = stored
        }

      }
      ,
      getFilter: function (message) {

        this.filters = []
        this.stocks = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''


        axios.get("http://127.0.0.1:8000/search_Filters/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            search_item: message,
            start_date: this.date_start,
            end_date: this.date_end
          }
        }).then((response) => {
          if (response.data == "Token expired") {
            alert('لطفا دوباره وارد شوید')
            router.push('/')
          }

          localStorage.setItem('get_filters_columns', JSON.stringify(response.data.columns))
          var temp = []
          for (var i = 0; i < response.data.search_result.length; i++) {
            temp.push({
              "isNegative": 0,
              "data": response.data.search_result[i]
            })
          }


          for (var i = 0; i < temp.length; i++) {
            for (var j = 6; j < 7; j++) {
              if (Number(temp[i].data[j] < 0)) {
                temp[i].isNegative = 1
              }
            }
          }


          for (var i = 0; i < temp.length; i++) {
            for (var j = 8; j < temp[i].data.length - 1; j++) {
              temp[i].data[j] = Number(temp[i].data[j]).toFixed(1)
            }
          }


          for (var i = 0; i < temp.length; i++) {
            for (var j = 0; j < temp[i].data.length; j++) {
              if (j == 3) {
                temp[i].data[j] = {
                  "data": temp[i].data[j],
                  "isLink": 1,
                  "isFilter": 1
                }
              } else {
                temp[i].data[j] = {
                  "data": temp[i].data[j],
                  "isLink": 0,
                  "isFilter": 1
                }
              }

            }
          }


          this.search_result = temp
          this.column_names = response.data.columns
          this.page_info = message
        })

      }
      ,
      getStock: function (message) {
        // use event here as well as id
        this.filters = []
        this.stocks = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''
        axios.get("http://127.0.0.1:8000/search_Stocks/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            search_item: message,
            start_date: this.date_start,
            end_date: this.date_end
          }
        }).then((response) => {

          if (response.data == "Token expired") {
            alert('لطفا دوباره وارد شوید')
            router.push('/')
          }

          for (var i = 0; i < response.data.search_result.length; i++) {
            this.search_result = this.search_result.concat({
              "isNegative": 0,
              "isMean3": 0,
              "isMean12": 0,
              "data": response.data.search_result[i]
            })
          }

          for (var i = 0; i < this.search_result.length; i++) {
            for (var j = 0; j < 7; j++) {
              if (j == 6) {
                if (Number(this.search_result[i].data[j] < 0)) {
                  this.search_result[i].isNegative = 1
                }
              }
            }
          }

          for (var i = 0; i < this.search_result.length; i++) {
            for (var j = 8; j < this.search_result[i].data.length - 1; j++) {
              this.search_result[i].data[j] = Number(this.search_result[i].data[j]).toFixed(1)
            }
          }

          for (var i = 0; i < this.search_result.length; i++) {
            for (var j = 0; j < this.search_result[i].data.length; j++) {
              if (j == 3) {
                this.search_result[i].data[j] = {"data": this.search_result[i].data[j], "isLink": 1, "isStock": 1}
              } else {
                this.search_result[i].data[j] = {"data": this.search_result[i].data[j], "isLink": 0, "isStock": 1}
              }
            }
          }

          for (var i = 0; i < this.search_result.length; i++) {
            for (var j = 0; j < this.search_result[i].data.length; j++) {

              if (j == 3) {
                if (this.search_result[i].data[j].data.toString().includes("3 ماه")) {
                  if (this.mean3_Flag == 1) {
                    this.search_result[i].isMean3 = 1
                  }
                  if (this.mean3_Flag == 0) {
                    this.search_result[i].isMean3 = null
                  }
                }
                if (this.search_result[i].data[j].data.toString().includes("12 ماه")) {
                  if (this.mean12_Flag == 1) {
                    this.search_result[i].isMean12 = 1
                  }
                  if (this.mean12_Flag == 0) {
                    this.search_result[i].isMean12 = null
                  }
                }
              }
            }
          }


          this.column_names = response.data.columns
          this.page_info = message
        })
      }
      ,
      mean3_Switch() {
        if (this.mean3_Flag == 0) {
          this.mean3_Flag = 1
        } else {
          this.mean3_Flag = 0
        }
        var page_info = this.page_info

        this.getStock(page_info)

        if (this.page_info == '') {
          this.getFilter(page_info)
        }
      }
      ,
      mean12_Switch() {
        if (this.mean12_Flag == 0) {
          this.mean12_Flag = 1
        } else {
          this.mean12_Flag = 0
        }
        var page_info = this.page_info

        this.getStock(page_info)

        if (this.page_info == '') {
          this.getFilter(page_info)
        }
      }
      ,
      add_to_favorites: function (message) {
        alert(message)
      }
      ,
      date_end_changed() {
        var page_info = this.page_info

        this.getStock(page_info)

        if (this.page_info == '') {
          this.getFilter(page_info)
        }
      }
      ,
      date_start_changed() {
        var page_info = this.page_info

        this.getStock(page_info)

        if (this.page_info == '') {
          this.getFilter(page_info)
        }
      },
      log_out() {
        axios.get("http://127.0.0.1:8000/logout/", this.config).then((response) => {
          alert('شما با موفقیت خارج شدید')
        })
        sessionStorage.clear()
        router.push("/")
      },
      add_to_favorites_filters(item) {
        localStorage.removeItem('favorite_filters')
        axios.get("http://127.0.0.1:8000/add_to_favorites_filters/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            favorite_item: item,
          }
        }).then((response) => {
          alert('فیلتر اضافه شد')
        })
      },
      show_favorite_filters() {
        this.filters = []
        this.stocks = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''
        if (localStorage.getItem('favorite_filters') == [] || localStorage.getItem('favorite_filters') === null) {
          var temp = []
          axios.get("http://127.0.0.1:8000/show_favorites_filters/", this.config).then((response) => {
            if (response.data == "Token expired") {
              alert('لطفا دوباره وارد شوید')
              router.push('/')
            }
            for (var i = 0; i < response.data.search_result.length; i++) {
              temp.push(response.data.search_result[i])
            }
            localStorage.setItem('favorite_filters', JSON.stringify(temp))
            var stored = JSON.parse(localStorage.getItem("favorite_filters"))
            this.filters = stored
          })

        } else {
          var stored = JSON.parse(localStorage.getItem("favorite_filters"))
          this.filters = stored
        }
      },
      remove_favorites_filters(item) {
        localStorage.removeItem('favorite_filters')
        axios.get("http://127.0.0.1:8000/remove_favorites_filters/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            favorite_item: item,
          }
        }).then((response) => {
          this.show_favorite_filters()
        })
      },


      add_to_favorites_stocks(item) {
        localStorage.removeItem('favorite_stocks')
        axios.get("http://127.0.0.1:8000/add_to_favorites_stocks/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            favorite_item: item,
          }
        }).then((response) => {
          alert('سهم اضافه شد')
        })
      },
      show_favorite_stocks() {
        this.filters = []
        this.stocks = []
        this.search_result = []
        this.column_names = []
        this.page_info = ''
        if (localStorage.getItem('favorite_stocks') == [] || localStorage.getItem('favorite_stocks') === null) {
          var temp = []
          axios.get("http://127.0.0.1:8000/show_favorites_stocks/", this.config).then((response) => {
            if (response.data == "Token expired") {
              alert('لطفا دوباره وارد شوید')
              router.push('/')
            }
            for (var i = 0; i < response.data.search_result.length; i++) {
              temp.push(response.data.search_result[i])
            }
            localStorage.setItem('favorite_stocks', JSON.stringify(temp))
            var stored = JSON.parse(localStorage.getItem("favorite_stocks"))
            this.stocks = stored
          })

        } else {
          var stored = JSON.parse(localStorage.getItem("favorite_stocks"))
          this.stocks = stored
        }
      },
      remove_favorites_stocks(item) {
        localStorage.removeItem('favorite_stocks')
        axios.get("http://127.0.0.1:8000/remove_favorites_stocks/", {
          headers: {
            'Authorization': `Token ${sessionStorage.getItem('authToken')}`,
          },
          params: {
            favorite_item: item,
          }
        }).then((response) => {
          this.show_favorite_stocks()
        })
      }


    }
  }


</script>





