<template>
  <div class="container" v-if="show_page_1 == '1'">
    <h1 class="text-center" style="font-family: 'Segoe UI', Tahoma;font-size: 30px">خوش آمدید</h1>
    <div id="auth-container" class="row">
      <div class="col-sm-4 offset-sm-4">
        <ul class="nav nav-tabs nav-justified" id="myTab" role="tablist">
          <li class="nav-item">
            <a class="nav-link active" id="signup-tab" data-toggle="tab" role="tab"
               aria-controls="signup" aria-selected="true" @click="Sign_Up" style="font-family: 'Segoe UI', Tahoma">ثبت
              نام</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" id="signin-tab" data-toggle="tab" role="tab" aria-controls="signin"
               aria-selected="false" @click="Sign_In" style="font-family: 'Segoe UI', Tahoma">ورود</a>
          </li>
        </ul>

        <div class="tab-content" id="myTabContent">

          <div class="tab-pane fade show active" id="signup" role="tabpanel" aria-labelledby="signin-tab"
               v-if="show_signup == '1'">
            <form @submit.prevent="signUp">
              <div class="form-group">
                <input v-model="email" type="email" class="form-control" id="email" placeholder="Email Address"
                       required>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <input v-model="username" type="text" class="form-control" id="username" placeholder="Username"
                         required>
                </div>
                <div class="form-group col-md-6">
                  <input v-model="password" type="password" class="form-control" id="password" placeholder="Password"
                         required>
                </div>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox" id="toc" required>
                  <label class="form-check-label" for="gridCheck" style="font-family: 'Segoe UI', Tahoma">
                    شرایط را خوانده و می پذیرم
                  </label>
                </div>
              </div>
              <button type="submit" class="btn btn-block btn-primary" style="font-family: 'Segoe UI', Tahoma">ثبت نام
              </button>
            </form>
          </div>

          <div class="tab-pane fade show active " id="signin" role="tabpanel" aria-labelledby="signin-tab"
               v-if="show_signin == '1'">
            <form @submit.prevent="signIn">
              <div class="form-group">
                <input v-model="username" type="text" class="form-control" id="username" placeholder="Username"
                       required>
              </div>
              <div class="form-group">
                <input v-model="password" type="password" class="form-control" id="password" placeholder="Password"
                       required>
              </div>
              <button type="submit" class="btn btn-block btn-primary" style="font-family: 'Segoe UI', Tahoma">ورود
              </button>
            </form>
          </div>

        </div>
      </div>
    </div>
  </div>


  <div v-else>
    <div id="mail_confirm">
      <p style="margin-top: 200px;font-family: 'Segoe UI', Tahoma;font-size: 20px">کد فعالسازی ارسال شده به ایمیلتان را
        وارد کنید</p>
      <form>
        <div class="form-group">
          <input
            style="width: 200px;;margin-top: 0px;position: absolute;top:  50%;left: 50%;transform: translate(-50%,-50%);"
            v-model="confirmation_code" class="form-control" id="code" placeholder="کد ارسال شده"
            required>
        </div>
        <button type="submit" class="btn btn-block btn-primary"
                style="font-family: 'Segoe UI', Tahoma;width: 200px;position: absolute;top:  50%;left: 50%;transform: translate(-50%,-50%);margin-top: 60px"
                @click="send_Code">
          تایید
        </button>
      </form>
    </div>

  </div>


</template>

<script>
  import axios from "axios";

  const $ = window.jQuery
  export default {
    data() {
      return {
        username: '',
        password: '',
        email: '',
        show_page_1: 1,
        show_page_2: 0,
        show_signup: 1,
        show_signin: 0,
      }
    },
    methods: {
        signIn_redirect() {
        localStorage.clear()
        const credentials = {email: 'ali.ghadimzadeh@gmail.com', password: '4770523Ab'}
        alert("this is sign in redirect")
        axios.get('http://localhost:8000/auth/token/login/', {
          params: {email: this.email, password: this.password}
        }).then(response => {
          alert("this is axios response")
          if (response.data == "Invalid credentials"){
            alert("Invalid credentials")
            this.$router.push('/')
            this.Sign_Up()

          }
          else{

          sessionStorage.setItem('authToken', response.data.auth_token)
          sessionStorage.setItem('username', this.username)

          this.$router.push('/blog')
            }
        })
          alert("this is sign in redirect end")
      },
        signIn() {
        localStorage.clear()
        const credentials = {email: 'ali.ghadimzadeh@gmail.com', password: '4770523Ab'}
        axios.get('http://localhost:8000/auth/token/login/', {
          params: {email: document.getElementById("username").value, password: document.getElementById('password').value}
        }).then(response => {
          alert("this is axios response")
          if (response.data == "Invalid credentials"){
            alert("Invalid credentials")
            this.$router.push('/')
            this.Sign_Up()

          }
          else{

          sessionStorage.setItem('authToken', response.data.auth_token)
          sessionStorage.setItem('username', this.username)

          this.$router.push('/blog')
            }
        })
      },
      Sign_In() {
        this.show_signin = 1
        this.show_signup = 0
        document.getElementById("signin-tab").setAttribute("class", "nav-link active")
        document.getElementById("signup-tab").setAttribute("class", "nav-link")
      },
      Sign_Up() {
        this.show_signin = 0
        this.show_signup = 1
        document.getElementById("signin-tab").setAttribute("class", "nav-link")
        document.getElementById("signup-tab").setAttribute("class", "nav-link active")
      },
      data_auth() {

        return {
          email: this.email, username: this.username, password: this.password
        }
      },
      signUp() {
        axios.get("http://127.0.0.1:8000/auth/users/", {
          params: this.data_auth()
        }).then((response) => {
          localStorage.setItem('temp_token', JSON.stringify(response.data.auth_token))

          if(response.data == "User exists")
          {
            alert("این کاربر قبلا ثبت نام کرده است")
          }
          else
          {
          this.show_page_1 = 0
            this.show_page_2 = 1
            }
        })


      },
      send_Code() {


        axios.get("http://127.0.0.1:8000/code_verification/", {
          headers: {
            'Authorization': `Token ${localStorage.getItem('temp_token')}`,
          },
          params: {
            verification_code: document.getElementById('code').value,
          }
        }).then((response) => {

          if (response.data == "Mail confirmation sucsess") {
            alert(response.data)
            this.signIn_redirect()

            alert("secuess")
          } else {
            this.show_page_1 = 1
            alert("failed")
          }
        })

      }


    }
  }
</script>

<style scoped>
  #auth-container {
    margin-top: 50px;
  }

  li {
    cursor: pointer;
  }

  .tab-content {
    padding-top: 20px;
  }
</style>







